import React, { useState } from "react";

type fn = (toastMessage: string) => void;

export interface ISchedulePageState {
  onForceRefresh: Function;
  setForceRefreshFunc: Function;
}

export interface ISchedulePageContext extends ISchedulePageState {}

const INITIAL_MESSAGES_CONTEXT: ISchedulePageContext = {
  onForceRefresh: (s: string) => {
    console.log(s);
  },
  setForceRefreshFunc: (func: fn) => {
    console.log(func);
  },
};

export const SchedulePageContext = React.createContext<ISchedulePageContext>(
  INITIAL_MESSAGES_CONTEXT
);

function initialFunc(toastMessage: string) {
  console.log(toastMessage);
}

// --------------------------------------------------------------------------------------
//
// --------------------------------------------------------------------------------------
export const SchedulePageContextProvider = (props: any) => {
  const [refreshFunc, setRefreshFunc] = useState({ func: initialFunc });

  const onSet = (newFunc: fn) => {
    setRefreshFunc({ func: newFunc });
  };

  return (
    <SchedulePageContext.Provider
      value={{
        onForceRefresh: refreshFunc.func,
        setForceRefreshFunc: onSet,
      }}
    >
      {props.children}
    </SchedulePageContext.Provider>
  );
};
