import React, { useState } from "react";
// import "./WeekChooser.scss";
import "../../global-styles.scss";
import { IonIcon, IonDatetime } from "@ionic/react";
import { arrowBack, arrowForward, caretDown } from "ionicons/icons";
import { addDays, format, isSameDay, isToday, parseISO } from "date-fns";

interface WeekChooserProps {
  startDate: Date;
  setStartDate: (date: Date) => void;
  initialSelectedDate: Date | null;
  onDateSelected: ((date: Date) => void) | null;
}

// --------------------------------------------------------------------------------------
//
// --------------------------------------------------------------------------------------
const WeekChooser: React.FC<WeekChooserProps> = ({
  startDate,
  setStartDate,
  initialSelectedDate,
  onDateSelected,
}: WeekChooserProps) => {
  const [selectedDate, setSelectedDate] = useState(initialSelectedDate);

  // --------------------------------------------------------------------------------------
  const onDateClick = (date: Date) => {
    if (onDateSelected !== null) {
      setSelectedDate(date);
      onDateSelected(date);
    }
  };

  return (
    <div className="chooser">
      <div className="top">
        <button
          onClick={() => setStartDate(addDays(startDate, -7))}
          className="arrow"
          key={-1}
        >
          <IonIcon mode="ios" icon={arrowBack} />
        </button>

        <div className="month-year-container">
          <div className="datetime-picker-container">
            <IonDatetime
              className="datetime-picker"
              displayFormat="MMM YYYY"
              pickerFormat="MMM D YYYY"
              min="2005-01-01"
              max="2030-01-01"
              value={startDate.toISOString()}
              onIonChange={(e) => {
                setStartDate(parseISO(e.detail.value!));
              }}
            />
            <IonIcon mode="md" icon={caretDown} />
          </div>
        </div>
        
        <button
          onClick={() => setStartDate(addDays(startDate, 7))}
          className="arrow"
          key={7}
        >
          <IonIcon mode="ios" icon={arrowForward} />
        </button>
      </div>
      <div className="weekday-list">
        {[0, 1, 2, 3, 4, 5, 6].map((num: number) => {
          const thisDate = addDays(startDate, num);
          const isTodayClass = isToday(thisDate) ? "today" : "not-today";
          const selectedDateClass =
            selectedDate !== null && isSameDay(thisDate, selectedDate)
              ? "selected-date"
              : "";

          return (
            <button
              key={num}
              className="weekday-button"
              style={{ outline: "none" }}
              onClick={() => {
                onDateClick(thisDate);
              }}
            >
              <div className={selectedDateClass}>
                <div className="weekday-abbr">{format(thisDate, "EEE")}</div>
                <div className={isTodayClass} />
                <div className="weekday-num">{format(thisDate, "d")}</div>
              </div>
            </button>
          );
        })}
      </div>
    </div>
  );
};

export default WeekChooser;
